.imageslider-image-container {
  height: 100%;
  overflow: visible;

  img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
}

.slick-slider {
  height: 100%;
}

.slick-list {
  height: 100%;
}

.slick-track {
  display: flex;
  height: 100%;
}

.slick-track .slick-slide {
  display: flex;
  height: 100%;
}
