@import "../../../partials";

%title-texts {
    font-size: $primary-large-font-size;
    font-weight: 200;
}

#returninguser-profile-container,
#returninguser-feed-container {
    @include flex-display-row;
}

#returninguser-recent-posts-container {
    margin-top: 2.5%;
    margin-bottom: 2.5%;
}

#returninguser-recent-work-button {
    border: none;
    outline: none;
    background-color: transparent;
}

#returninguser-recent-work-title{
    text-decoration: none;
    color: black;
}
.returninguser-title {
    @extend %title-texts;
}

.returninguser-main-row {
    margin-top: 2.5%;
    margin-bottom: 2.5%;
    padding: 2vh;

    #returninguser-recent-work-container {
        @include flex-display-column;
    }

    #returninguser-profile-photo {
        object-fit: cover;
        border-radius: 50%;
        @include medium-profile-photo;
    }

    .returninguser-profile-text-container {
        text-align: center;
    }

    .returninguser-profile-column {
        @include flex-display-column;
        margin: auto;
    }

    #returninguser-pursuit-info-table {
        width: 100%;
        border-collapse: collapse;
        text-align: center;

        td,
        th {
            padding: 8px;
        }
    }
}

.returninguser-row {
    @include flex-display-row;
}

#returninguser-infinite-scroll-container {
    margin: auto;
    width: 800px;
    @include flex-display-column;

    .returninguser-feed-object-container {
        margin: 100px;
        background-color: grey;
    }
}
