//Sizes
// Colors
$primary-color: #ed4071;
$primary-text-color: black;
$primary-calc-medium-font-size: calc(10px + 2vmin);
$primary-extra-large-font-size: 30px;
$primary-large-font-size: 22.5px;
$primary-medium-font-size: 15px;

$small-post-height: 74vh;
$small-button-window: 19vh;

@mixin flex-display-row {
    display: flex;
    flex-direction: row;
}

@mixin flex-display-column {
    display: flex;
    flex-direction: column;
}

@mixin main-profile-photo-container {
    height: 250px;
    width: 250px;
}

@mixin medium-profile-photo {
    height: 100px;
    width: 100px;
}

@mixin small-profile-photo {
    height: 75px;
    width: 75px;
}

@mixin tiny-profile-photo {
    height: 20px;
    width: 20px;
}

@mixin small-post-window {
    background-color: grey;
    position: fixed;
    margin-top: -38vh;
    margin-left: -38vw;
    top: 50%;
    left: 50%;
    z-index: 22;
    height: $small-post-height;
    width: 76vw;
    overflow: scroll;
}

@mixin large-post-window {
    background-color: white;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    z-index: 20;
    overflow: scroll;
}

@mixin small-button-window {
    position: absolute;
    // margin-top: 9.5vh;
    // margin-left: -19vw;
    // top: 50%;
    // left: 50%;
    z-index: 50;
    height: $small-button-window;
    // width: 38vw;
}

@mixin long-post-editor-container {
    margin: 0 auto;
}

@mixin centered-vertical-button-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@mixin short-post-hero-text-container {
    height: 100%;
    width: 65%;
    margin: auto;
    text-align: center;
    justify-content: center;
}

@mixin short-post-hero-image-container {
    height: 100%;
    width: 65%;
    margin: auto;
    text-align: center;
    justify-content: center;
}

@mixin short-post-side-container {
    width: 35%;
}

@mixin long-post-button-container {
    width: 70%;
    margin: auto;
    position: relative;
}

@mixin long-post-text-container {
    width: 70%;
    margin: auto;
}

@mixin no-select {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently 
    supported by Chrome, Edge, Opera and Firefox */
}
